    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', 'sans-serif';
}
.container {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(135deg, #153677, #4e085f);
    padding: 10px;
}
.todo-app {
    width: 100%;
    max-width: 540px;
    background-color: #fff;
    margin: 100px auto 20px;
    padding: 40px 30px 70px;
    border-radius: 10px;
}
.todo-app h2 {
    color: #002765;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.todo-app img {
    width: 200px;
    margin-left: 10px;
}
.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #edeef0;
    border-radius: 30px;
    padding-left: 20px;
    margin-bottom: 25px;
}
input {
    flex: 1;
    border: none;
    outline: none;
    background: transparent;
    padding: 10px;
}
button { 
    border: none;
    outline: none;
    padding: 16px 50px;
    background: #ff5945;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    border-radius: 40px;
}
ul li {
    list-style: none;
    font-size: 17px;
    padding: 12px 8px 12px 50px;
    user-select: none;
    cursor: pointer;
    position: relative;
}
ul li::before {
    content: '';
    position: absolute;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: center;
    top: 6px;
    left: 8px;
}
ul li.checked {
    color: #555;
    text-decoration: line-through;
}
ul li.checked::before {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
ul li span {
    position: absolute;
    right: 0;
    top: 5px;
    width: 40px;
    height: 40px;
    font-size: 22px;
    color: #555;
    line-height: 40px;
    text-align: center;
    border-radius: 50px;
}
ul li span:hover {
    background-color: #edeef0;
}`, "",{"version":3,"sources":["webpack://./src/assets/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,oCAAoC;AACxC;AACA;IACI,WAAW;IACX,iBAAiB;IACjB,qDAAqD;IACrD,aAAa;AACjB;AACA;IACI,WAAW;IACX,gBAAgB;IAChB,sBAAsB;IACtB,uBAAuB;IACvB,uBAAuB;IACvB,mBAAmB;AACvB;AACA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,mBAAmB;AACvB;AACA;IACI,YAAY;IACZ,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;AACvB;AACA;IACI,OAAO;IACP,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,aAAa;AACjB;AACA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,mBAAmB;IACnB,WAAW;IACX,eAAe;IACf,eAAe;IACf,mBAAmB;AACvB;AACA;IACI,gBAAgB;IAChB,eAAe;IACf,2BAA2B;IAC3B,iBAAiB;IACjB,eAAe;IACf,kBAAkB;AACtB;AACA;IACI,WAAW;IACX,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,yDAA6C;IAC7C,sBAAsB;IACtB,2BAA2B;IAC3B,QAAQ;IACR,SAAS;AACb;AACA;IACI,WAAW;IACX,6BAA6B;AACjC;AACA;IACI,yDAA2C;AAC/C;AACA;IACI,kBAAkB;IAClB,QAAQ;IACR,QAAQ;IACR,WAAW;IACX,YAAY;IACZ,eAAe;IACf,WAAW;IACX,iBAAiB;IACjB,kBAAkB;IAClB,mBAAmB;AACvB;AACA;IACI,yBAAyB;AAC7B","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    font-family: 'Poppins', 'sans-serif';\n}\n.container {\n    width: 100%;\n    min-height: 100vh;\n    background: linear-gradient(135deg, #153677, #4e085f);\n    padding: 10px;\n}\n.todo-app {\n    width: 100%;\n    max-width: 540px;\n    background-color: #fff;\n    margin: 100px auto 20px;\n    padding: 40px 30px 70px;\n    border-radius: 10px;\n}\n.todo-app h2 {\n    color: #002765;\n    display: flex;\n    align-items: center;\n    margin-bottom: 20px;\n}\n.todo-app img {\n    width: 200px;\n    margin-left: 10px;\n}\n.row {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    background: #edeef0;\n    border-radius: 30px;\n    padding-left: 20px;\n    margin-bottom: 25px;\n}\ninput {\n    flex: 1;\n    border: none;\n    outline: none;\n    background: transparent;\n    padding: 10px;\n}\nbutton { \n    border: 